import { element } from 'prop-types'
import React from 'react'

// ========= admin ============
const Dashboard = React.lazy(() => import('./views/admin/dashboard/Dasshboard'))
const Tenant = React.lazy(()=>import('./views/admin/tenant/Tenant'))
const Jobs = React.lazy(()=>import('./views/admin/jobs/Jobs'))
const Customer = React.lazy(() => import('./views/admin/customers/Customers'))
const Users = React.lazy(() => import('./views/admin/users/Users'))
const Candidates = React.lazy(() => import('./views/admin/candidates/Candidates'))
const Communications = React.lazy(() => import('./views/admin/communications/events/Events'))
const Events = React.lazy(() => import('./views/admin/communications/events/Events'))
const HelpCenter = React.lazy(() => import('./views/admin/communications/helpCenter/HelpCenter'))
const Legals = React.lazy(() => import('./views/admin/communications/legals/Legals'))
const Settings = React.lazy(() => import('./views/admin/settings/Settings'))

// =========== customers =========

const LoginPage = React.lazy(()=>import('./views/pages/login/Login'))


const routes = [
  { path: '/', exact: true, name: 'Home', element: Dashboard},

  // ============ admin ==============
  { path: '/dashboard', name: 'Dashboard', element: Dashboard },
  { path : '/tenant', name: 'Tenant', element:Tenant},
  { path : '/jobs', name: 'Jobs', element:Jobs},
  { path: "/customers", name: "Customers", element: Customer },
  { path: '/users', name: 'Users', element: Users },
  { path: '/candidates', name: "Candidates", element: Candidates },
  { path: "/communications", name: "Communications", element: Communications, exact: true },
  { path: "/communications/events", name: "Events", element: Events },
  { path: '/communications/helpCenter', name: "Help Center", element: HelpCenter },
  { path: '/communications/legals', name: "Legals", element: Legals },
  { path: "/settings", name: "Settings", element: Settings },

  // ============= custemers =========

  // { path: "/login", name:"Login", element: LoginPage}
]

export default routes
